<template lang="html">
  <div class="nav-table-cell" :class="[cellclass]" :style="{'height': height}">
    <div :class="[titleclass]">{{title}}</div>
    <div :class="[numclass]">{{number}}</div>
    <i v-if="showarrow" class="fa fa-angle-right fa-lg nav-table-cell__arrow" aria-hidden="true"></i>
	</div>
</template>

<script>
export default {
  props: {
    title: String,
    number: Number,
    titleclass: {
      type: String,
      default: ''
    },
    numclass: {
      type: String,
      default: ''
    },
    cellclass: {
      type: String,
      default: ''
    },
    showarrow: {
      type: Boolean,
      default: false
    },
    height: {
      type: String,
      default: ''
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
.nav-table-cell {
    display: table-cell;
    text-align: center;
    font-size: .3rem;
    vertical-align: middle;
    position: relative;
}
.nav-table-cell:last-child {
    border: none
}
.nav-table-cell--red {
    border-right: 1px solid @whiteColor;
    background: @navBarRed;
    height: 1.4rem;
    color: @whiteColor;
    line-height: 0.42rem;
}
.nav-table-cell--white {
    border-right: 1px solid @lineColor;
    background: @whiteColor;
    height: 1.4rem;
    color: @defaultColor;
    line-height: 0.42rem;
}
.nav-table-cell__arrow {
    position: absolute;
    right: .3rem;
    top: 50%;
    transform: translateY(-50%);
}
.f-redColor {
  color: @redColor
}
</style>



